.Box {
  border: 1px solid #333;
  border-radius: 5px;
  width: 400px;
  margin-bottom: 5px; }

.Box-header {
  background-color: #EEE;
  border-bottom: 1px solid #333; }
  .Box-header h1 {
    margin: 0 0;
    color: #333;
    font-size: 1.1em; }

.Box-header, .Box-content {
  padding: 5px 10px; }

input {
  font-size: 1.2em; }

.Input-addon {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1em; }

.Input-addon-field {
  -ms-flex: 1 1;
      flex: 1 1; }

.Input-addon-field:not(:first-child) {
  border-left: 0; }

.Input-addon-field:not(:last-child) {
  border-right: 0; }

.Input-addon-item {
  background-color: rgba(147, 128, 108, 0.1);
  color: #666; }

.Input-addon-field, .Input-addon-item {
  border: 1px solid rgba(147, 128, 108, 0.25);
  padding: 0.25em 0.5em; }

.Input-addon-field:first-child, .Input-addon-item:first-child {
  border-radius: 2px 0 0 2px; }

.Input-addon-field:last-child, .Input-addon-item:last-child {
  border-radius: 0 2px 2px 0; }



